@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

.menu{
    background-color: #343A40 !important;
   
}

.menu-brand{
    margin-right: 50px !important;
}

.coloring{
    color: #fff !important;
    margin-left: 40px;
  }

  .home{
      visibility: hidden;
      margin-left: 15px;
  }

.red{
    background-color: blue !important;
}

/* modal style */

.close-modal {
    position: absolute;
    top: 10px;
    right: 10px;
    padding: 5px 7px;
    background-color: #fff;
    font-size: 45px;
    border: none;
}

.form-input{
    width: 50%;
    border: none;
    outline: none;
    padding-bottom: 10px;
    border-bottom: 1px solid #ccc ;
    margin-bottom: 45px;
    
}

.form-label{
    color: lightskyblue;
    font-weight: bold;
    font-size: 18px;
    font-family: 'Poppins', sans-serif;
   
}

.login-heading{
    margin-bottom: 70px;
    font-size: 30px;
}

.form-input-footer{
    width: 100%;
    padding-bottom: 10px;
    border: 1px solid #ccc ;
    margin-bottom: 45px;
    
}




/* footer start */

.footer-section{
    background-color: #1D495E !important;
    padding: 40px;
}

.footer-flex{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}

.btn{
    color: #fff;
    padding: 10px;
}

.btn:hover{
    color: #fff;
}

.footer-center{
    display: flex;
    align-items: center;
    font-size: 30px;
    color: white;
}


/* banner start */

.banner-section{
    background-color: #1C6697;
    margin-bottom: 20px;
    padding: 80px;
}

.banner-flex{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.banner-one{
    color: #fff;
    text-transform: uppercase;
    font-size: 42px;
    font-weight: 100;
    text-shadow: 1px 1px 4px #212121;
    margin-bottom: 15px;
}

.banner-two{
    color: #fff;
    text-transform: uppercase;
    align-items: center;
    display: flex;
   font-size: 18px;
    font-weight: 300;
    line-height: 38px;
}

@media (max-width:480px){
   .banner-one{
    font-size: 22px;
    font-weight: 50;
    }

    .banner-two{
       display: none;
    }
}

/* banner end */


/* quiz start */

.quiz-heading{
    text-align: center;
    margin-bottom: 20px;
}

.quiz-questions{
  padding: 30px;
    box-shadow: 0 2px 12px 1px #648ae02e;
    margin-bottom: 50px;
}

.quiz-heading-two{
    padding-bottom: 10px;
}

.quiz-label{
    margin-left: 5px;
    margin-bottom: 5px;
}

.last-label{
    margin-bottom: 18px;
}

.test-btn{
    margin-top: 20px;
}

#handletest{
    margin-top: 15px;
    color: green;
}

